import { Link } from "react-router-dom";
import slate from "../images/Slate.png";

export default function Cuire() {
  const items = [
    "Saucisson  Cuire Nature 400g",
    "Saucisson  Cuire  la\n Pistache 400g",
    "Sabodet  Cuire 600g",
    "Saucisson  Cuire  la\n Truffe Noire Franaise 400g \n(Dcembre uniquement)",
  ];

  return (
    <section className="cuires">
      <div className="cuires-cover"></div>
      <div className="desc">
        <h1>Gamme Saucissons  Cuire</h1>
      </div>
      <img src={slate} alt="Carte menu ardoise" />
      <ul className="cuires_items">
        {items.map((item, i) => {
          return <li key={i}>{item}</li>;
        })}
      </ul>
      <Link className="first_a" to="/nos-saucissons/traditionnelle">
        <button>Voir la gamme Traditionnelle</button>
      </Link>
      <Link to="/nos-saucissons/spcialit">
        <button>Voir la gamme Spcialits</button>
      </Link>
    </section>
  );
}
