      <h2>
        Vous allez recevoir dici quelques minutes un mail de confirmation
        denvoi sur ladresse mail que vous avez indique.
        <br />
        Sil napparat pas dans votre bote de rception, pensez  vrifier vos
        courriers indsirables ou SPAM. 
      </h2>
